import { cilPencil, cilTrash } from '@coreui/icons';
import CIcon from '@coreui/icons-react';
import { CButton, CCard, CCardBody, CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from '@coreui/react'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify'

const Menu = () => {
  const [menu, setmenu] = useState([]);
  const [updateList, setUpdateList] = useState({}) 

  useEffect(() => {
    var requestOptions = {
      method: 'POST',
      redirect: 'follow'
    };

    let url =  process.env.REACT_APP_API_URL+"menu/list"
    fetch(url, requestOptions)
      .then(response => response.json())
      .then(result => { console.log(result.data); setmenu(result.data) })
      .catch(error => console.log('error', error));
  }, [updateList]);

  const handleDelete =(arg)=>{
    let requestOptions = {
      method: 'POST',
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        "_id": arg
      }),
    };
    const confirmWrp =  confirm("Are you sure you want to delete this?");
    if(!confirmWrp){
      return;
    }
    fetch(process.env.REACT_APP_API_URL+"menu/delete", requestOptions)
    .then(response => response.json())
    .then(result => {
      if(result.status==200){
        toast.success(result.message);
        setUpdateList({"_id": arg});       
      }else{
        toast.error(result.message);
      }
    })
    .catch(error => console.log('error', error)); 
  }

  return (
    <>
    <ToastContainer />
      <div className='d-flex justify-content-between  align-items-center  mb-4'>
        <h1 className='headingType1'>Menu Listing</h1>
        <Link to={'/menu/add'} className='btn btn-primary'>Add</Link>
      </div>
      <CCard>
        <CCardBody>
          <CTable>
            <CTableHead>
              <CTableRow>               
                <CTableHeaderCell>
                  menu Name
                </CTableHeaderCell>
                <CTableHeaderCell>
                  menu Pages
                </CTableHeaderCell>
                <CTableHeaderCell>
                  Created At
                </CTableHeaderCell>               
                <CTableHeaderCell>
                  Action
                </CTableHeaderCell>
              </CTableRow>
            </CTableHead>
            <CTableBody>
              {menu.map((i, k) => (
                <CTableRow key={k}>
                  <CTableDataCell >
                    {i.menu_name}
                  </CTableDataCell>
                  <CTableDataCell>
                    <ul>
                    {i.menu_items.map((item, key)=>(<li key={key}>{item.page_name}</li>))}
                    </ul>
                    
                  </CTableDataCell>
                  <CTableDataCell>
                  {i.created_at}
                  </CTableDataCell>                 
                  
                  <CTableDataCell>
                    <ul className='actionBtnS'>
                      <li>
                        <CIcon onClick={()=>handleDelete(i._id)} icon={cilTrash} customClassName="nav-icon" />
                      </li>
                      <li>
                        <Link to={'/products/' + i._id}>
                          <CIcon icon={cilPencil} customClassName="nav-icon" />
                        </Link>
                      </li>
                    </ul>
                  </CTableDataCell>
                </CTableRow>
              ))}


            </CTableBody>
          </CTable>
        </CCardBody>
      </CCard>
    </>
  )
}

export default Menu
